  display: none;
}

/*************** mixins ***************/
/* Media queries */
/* remove list format */
/*************** header ***************/
header {
  margin-left: 2.4rem;
  margin-right: 2.4rem;
}
header h1 {
  text-align: left;
  font-family: "Inconsolata", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 2.4rem;
  line-height: 2.5rem;
  letter-spacing: -0.08em;
  text-transform: uppercase;
  color: #333333;
}

/*************** content ***************/
main {
  margin: 6.4rem 1rem 11.4rem 2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  main {
    margin: 6.4rem 6rem 11.4rem 4rem;
  }
}
@media (min-width: 992px) {
  main {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin: 10rem 3rem 12rem 3rem;
  }
}
@media (min-width: 1200px) {
  main {
    margin: 15rem 6rem 15rem 5rem;
  }
}
@media (min-width: 1440px) {
  main {
    margin: 20rem 10.8rem 31rem 7rem;
  }
}
main picture > * {
  width: 100%;
}
@media (min-width: 768px) {
  main picture > * {
    width: 105rem;
    padding-right: 1.5rem;
  }
}
@media (min-width: 992px) {
  main picture > * {
    padding-right: 3rem;
  }
}
@media (min-width: 1200px) {
  main picture > * {
    padding-right: 6rem;
  }
}
@media (min-width: 1440px) {
  main picture > * {
    padding-right: 10rem;
  }
}
main .message {
  margin-top: 7rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
@media (min-width: 768px) {
  main .message {
    margin-top: 10rem;
  }
}
@media (min-width: 992px) {
  main .message {
    margin-top: unset;
  }
}
main .message h2 {
  font-family: "Space Mono", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 4.8rem;
  line-height: 7.1rem;
  letter-spacing: -0.035em;
  color: #333333;
}
@media (min-width: 1200px) {
  main .message h2 {
    font-size: 6.4rem;
    line-height: 9.5rem;
  }
}
main .message p {
  margin-top: 3rem;
  margin-bottom: 6rem;
  font-family: "Space Mono", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.7rem;
  letter-spacing: -0.035em;
  color: #4f4f4f;
}
@media (min-width: 1200px) {
  main .message p {
    width: 38rem;
    font-size: 2.4rem;
    line-height: 3.6rem;
  }
}
main .message a {
  padding: 2.4rem 4.3rem;
  width: fit-content;
  background-color: #333333;
  font-family: "Space Mono", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 2.1rem;
  letter-spacing: -0.035em;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
}

/*************** footer ***************/
footer .footer-text {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 1.7rem;
  text-align: center;
  color: #BDBDBD;
}
footer .footer-text .username {
  text-decoration: underline;
  font-weight: bold;
}